<template> 
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
  <div class="register">
<h1>CREATE AN ACCOUNT</h1>
<hr class="margin"><br><br>
<p> 
<label for="name">First Name</label><br><br>
<input id="name" v-model="name" type="text" name="name" pattern="[A-Za-z]{8}"> 
</p> 
<p> 
<label for="lname">Last Name</label> <br><br>
<input id="lname" v-model="lname" type="text" name="lname"> 
</p> 
<p> 
<label for="email">Email</label> <br><br>
<input id="email" v-model="email" type="email" name="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"> 
</p> 
<p> 
<label for="password">Password</label> <br><br>
<input id="password" v-model="password" type="password" name="password" pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"> 
</p> <br>
<p> 
<button @click="register" class="create">CREATE AN ACCOUNT</button>
</p> 
