<script> 
import {ref} from 'vue';
import firebase from 'firebase';

export default {
    name:'register',
   setup() {
        const email = ref("");
        const password = ref("");
        const register = () =>{
            firebase
                .auth()
                .createUserWithEmailAndPassword(email.value, password.value)
                .catch(err => alert(err.message));
            alert("Account Created Successfully")
            }
            return{
               register,
                email,
                password
            }
    }
          
}
</script> 
