import { createWebHistory, createRouter } from "vue-router";
import home from "@/components/home.vue";
import contact from "@/components/contact.vue";
import about from "@/components/about.vue";
import login from "@/components/login.vue";
import register from "@/components/register.vue";
import dog from "@/components/dog.vue";
import dgrooming from "@/components/dgrooming.vue";
import dfood from "@/components/dfood.vue";
import daccessories from "@/components/daccessories.vue";
import dtreats from "@/components/dtreats.vue";
import cat from "@/components/cat.vue";
import cgrooming from "@/components/cgrooming.vue";
import cfood from "@/components/cfood.vue";
import caccessories from "@/components/caccessories.vue";
import clit from "@/components/clit.vue";
import fish from "@/components/fish.vue";
import ffood from "@/components/ffood.vue";
import faqua from "@/components/faqua.vue";
import birds from "@/components/birds.vue";
import bfood from "@/components/bfood.vue";
import bsupp from "@/components/bsupp.vue";
import small from "@/components/small.vue";
import sfood from "@/components/sfood.vue";
import ssupp from "@/components/ssupp.vue";
import pedigree from "@/components/pedigree.vue";
import royal from "@/components/royal.vue";
import whiskas from "@/components/whiskas.vue";
import farmina from "@/components/farmina.vue";
import drools from "@/components/drools.vue";
const routes = [
  {
    path: "/",
    name: "home",
    components: {
      default:home
      }
  },
  {
    path: "/contact",
    name: "contact",
    component: contact,
  },
  {
    path: "/about",
    name: "about",
    component: about,
  },
  {
    path: "/dog",
    name: "dog",
    component: dog,
    children:[
      {
      name: "dfood",
      path: "/dfood",
      component: dfood,
      },{
        name:"dgrooming",
        path:"/dgrooming",
        component:dgrooming,
      },{
        name:"daccessories",
        path:"/daccessories",
        component:daccessories,
      },
      {
        name:"dtreats",
        path:"/dtreats",
        component:dtreats,
      }
    ]
  },
  {
    path: "/cat",
    name: "cat",
    component: cat,
    children:[
      {
      name: "cfood",
      path: "/cfood",
      component: cfood,
      },{
        name:"cgrooming",
        path:"/cgrooming",
        component:cgrooming,
      },{
        name:"caccessories",
        path:"/caccessories",
        component:caccessories,
      },
      {
        name:"clit",
        path:"/clit",
        component:clit,
      }
    ]
  },
  {
    path: "/fish",
    name: "fish",
    component: fish,
    children:[
      {
      name: "ffood",
      path: "/ffood",
      component: ffood,
      },
      {
        name:"faqua",
        path:"/faqua",
        component:faqua,
      }
    ]
  },
  {
    path: "/birds",
    name: "birds",
    component: birds,
    children:[
      {
      name: "bfood",
      path: "/bfood",
      component: bfood,
      },
      {
        name:"bsupp",
        path:"/bsupp",
        component:bsupp,
      }
    ]
  },
  {
    path: "/small",
    name: "small",
    component: small,
    children:[
      {
      name: "sfood",
      path: "/sfood",
      component: sfood,
      },
      {
        name:"ssupp",
        path:"/ssupp",
        component:ssupp,
      }
    ]
  },
  {
    path: "/pedigree",
    name: "pedigree",
    component: pedigree,
  },
  {
    path: "/royal",
    name: "royal",
    component: royal,
  },
  {
    path: "/whiskas",
    name: "whiskas",
    component: whiskas,
  },
  {
    path: "/farmina",
    name: "farmina",
    component: farmina,
  },
  {
    path: "/drools",
    name: "drools",
    component: drools,
  },
  {
    path: "/login",
    name: "login",
    component: login,
  },
  {
    path: "/register",
    name: "register",
    component: register,
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes,
});
export default router;
