<template>
<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css">
<form id="app">
  <div class="login">
<h1>Customer Login</h1>
<h3>Registered Customer</h3>
<h2>No Account ?<br><br>
<a href="register"><input type="button" value="CREATE AN ACCOUNT"></a></h2>
<hr class="underline">
  <h3>If you have an account, sign in with your email address.</h3> 
<p> 
<label for="email">Email</label> 
<input id="email" v-model="email" type="email" name="email" pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"> 
</p> 
<p> 
<label for="password">Password</label> 
<input id="password" v-model="password" type="password" name="password" pattern="(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}"> 
</p> 
<p> 
<button @click="Login" class="log">Sign In</button>
<button @click="Logout" class="log2">Logout</button>
</p> 
  </div>
</form> 
</template> 
<script> 
import {ref} from 'vue';
import firebase from 'firebase';

export default {
    name:'Login',
   setup() {
        const email = ref("");
        const password = ref("");
        const Login = () =>{
            firebase
                .auth()
                .signInWithEmailAndPassword(email.value, password.value)
                .catch(err => alert(err.message));
            alert("Logged in Successfully")
            }
        const Logout = () =>{
            firebase
                .auth()
                .signOut()
                .catch(err => alert(err.message));
            alert("Logged out Successfully")
            }
            return{
               Login,
               Logout,
                email,
                password
            }
    }
          
}
</script> 
<style> 
li 
{
  list-style: none;
}
.login p{
  margin-left: 20%;
  font-size: 20px;
}
.login h1{
  margin-top:8%;
  margin-left: 20%;
}
.login h3{
  margin-left: 20%;
}
.login h2{
  float:right;
  margin-right: 20%;
}
.login input[type=email]{
font-size: 20px;
border: 2px solid ;
cursor: pointer;
border-radius: 5px;
outline:none;
margin-left: 3%;
}
.login input[type=password]{
font-size: 20px;
border: 2px solid ;
cursor: pointer;
border-radius: 5px;
outline:none;
margin-left: 1%;
}
.login .log{
font-size: 16px;
background:#0F7ADF;
color: white;
font-weight: bold;
cursor: pointer;
width:200px;
border-radius: 2px;
padding: 3px 0;
outline:none;
margin-left: 5%;
}
.login .log2{
font-size: 16px;
background:black;
color: white;
font-weight: bold;
cursor: pointer;
width:300px;
border-radius: 2px;
padding: 6px 0;
outline:none;
margin-left: 29%;
}
input[type=button]{
font-size: 14px;
background:black;
color: white;
font-weight: bold;
cursor: pointer;
width:300px;
border-radius: 2px;
padding: 6px 0;
outline:none;
}
hr.underline{
   border: 2px solid #F4CD30 ;
     width:40%;
     margin-left: 8%;
}
</style> 
